import React, { Component, Fragment } from 'react';

// style
import '../../style/bem-blocks/b-schedule-item/index.scss';

class ScheduleItem extends Component {
  
  render() {
    // const {startTime, finishTime, court, price, status} = this.props.dataScheduleItem;
    const { 
        startTime, 
        finishTime,
        status, // true -   , false -   
        price,
        freeCourt,
        courts
    } = this.props.dataScheduleItem;
    
    const { template } = this.props;

    const itemTrainer = () => (
        status ? freeCourt ?
            <Fragment>
                <div className='b-schedule-item__court-info'>
                    <p className='b-schedule-item__name-court'>{courts[0].name}</p>
                    <p className='b-schedule-item__address-court'>{courts[0].street}, . {courts[0].number}</p>
                    {courts.length > 1 ?
                        <p className="b-schedule-item__additional-court">  {courts.length - 1} ()</p>
                        :
                        null
                    }
                </div>
                <div className="b-schedule-item__click">    </div>
            </Fragment>
            :
            <div className="b-schedule-item__court-info">
                     .      ? -  <span className="b-schedule-item__tel">89176786243</span>
            </div>
            : null
    );

    const itemCourt = () => (
        status ? 
            <div className="b-schedule-item__click">    </div>
        : null
    );

    return (
        <div className="b-schedule-item">
            <div className="b-schedule-item__time-wrap">
                <div className="b-schedule-item__time">{startTime}</div>
                <div className="b-schedule-item__time b-schedule-item__time--finish">{finishTime}</div>
            </div>

            <div className="b-schedule-item__info">
                <div className={`b-schedule-item__state ${status ? 'b-schedule-item__state--free' : 'b-schedule-item__state--busy'}`}>
                    <span className={`b-schedule-item__state-name ${template === 'court' ? 'b-schedule-item__state-name--block' : ''}`}>{status ? ' ' : ' '} </span>
                    {status ? <span className="b-schedule-item__price">{price} /</span> : null}
                </div>
                { 
                    template === 'trainer' ?
                        itemTrainer()
                    :
                    template === 'court' ?
                        itemCourt()
                    : null
                }
                
            </div>
        </div>
    )
  }
}

export default ScheduleItem;


